:root {
	--xpos: 500;
	--ypos: 100px;

	--xpos2: 0px;
	--ypos2: 0px;

}

.header {
	text-align: center;
	margin: 10px;
	margin-top: 0;

}

.title {
	position: relative;
	font-size: 14pt;
	color: #e0e0e0;
	float: left;
	font-weight: lighter;
}

.about {
color: #6E93DB;
background: white;
width: 80px;
height: 40px;
border-radius: none;
border: 1px solid #6E93DB;
font-size: 10pt;
text-transform: uppercase;
float: right;
padding: 8px;
}

.about:hover {
color: white;
background: #6E93DB;

}

.wrapper {
  display: flex;
  flex-direction: column;
  margin: 15px;
  justify-content: center;
  text-align: left;

}

.grid {
  width: 350px;
  height: 350px;
  background: transparent;
  /* border: 1px solid #e0e0e0; */
	position: relative;
	margin: 0px;
	padding: 0px;
	border-right: 1px solid #e0e0e0;
	border-bottom: 1px solid #e0e0e0;
	z-index: 0;
	display: inline-block;
}

#activegrid {
  border: 3px solid blue;
}

/* .grid:hover {
  background: #fdfdfd;

} */

.row {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;

}

#no {
  display: none;
}

#yes {
  display: block;

}

.note {
	display: block;
	position: absolute;
		left:calc(var(--xpos) * 1px);
	  top: calc(var(--ypos) * 1px);
}

.bubble {

  width: 200px;
  height: 75px;
  background: white;
  border: 1px solid #e0e0e0;
	z-index: 1;
	color: grey;
	font-size: 10pt;
	border-radius: 8px;
}

.foodbubble {

  width: 200px;
  height: 70px;
  background: white;
  border: 1px solid #e0e0e0;
	z-index: 1;
	color: grey;
	font-size: 10pt;
	border-radius: 8px;
}

.noteheader {
	width: 100%;
	height: 10%;
	padding: 5%;
}

.notewrapper {
margin: 10px;
margin-top: 0px;
}

.notefooter {
	width: 100%;
	height: 10%;
	padding-right: 2%;
	text-align: right;
	position: relative;
	top: 0px;
}

.notelabel {
	color: #FF8CC0;
	text-transform: none;
	font-size: 8pt;
}

.foodnote {
	display: block;
	position: absolute;
	left:calc(var(--xpos2) * 1px);
  top: calc(var(--ypos2) * 1px);
}

.mood-button {
	font-size: 0;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	border: solid 1px transparent;
	padding: 5px;
	margin: 4px;
}

