.one {
	position: absolute;
	width: 25px;
	height: 25px;
	border-radius: 50%;
	left: 25px;
	z-index: 1;

}

.one:hover, .two:hover, .three:hover {
  transition: 0.5s;
  width: 35px;
  height: 35px;
	z-index: 1;

}

.two {
	position: absolute;
	width: 25px;
	height: 25px;
	border-radius: 50%;
