}

.toggle {
color: #e0e0e0;
background: white;
width: 100%;
display: flex;
flex-direction: row;
border: none;

}

.togglebutton {
color: #C4C4C4;
background: white;
width: 45%;
border-radius: 0px;
font-size: 10pt;
text-transform: none;
text-align: center;
padding: 20px;

}

.inactivetoggle {
	color: white;
	background: #e0e0e0;
	width: 45%;
	border-radius: 0px;
	font-size: 10pt;
	text-transform: none;
	text-align: center;
	padding: 20px;

}

.closemodal {
	color: #e0e0e0;
	width: 10%;
	background: white;
	font-size: 10pt;
	text-transform: none;
	text-align: center;
	padding: 20px;
	float: right;
}

#inactivetoggle {
color: #C4C4C4;
background: white;
width: 50%;
border-radius: 0px;
font-size: 10pt;
text-transform: uppercase;
text-align: center;
padding: 20px;
border-bottom: 1px solid #e0e0e0;
border-right: 1px solid #e0e0e0;
}

.inactivetoggle:hover, .closemodal:hover {
color: white;
background: grey;

}

.togglelabel {
font-size: 10pt;
text-transform: uppercase;
}

#popup {
display: block;
}

.popup {
	background-color: white;
	position: absolute;
	top: 18%;
	left: 33%;
	padding: 30px;
	border: 1px solid #e0e0e0;
	height: 300px;
	width: 500px;
	padding: 30px;
	z-index: 1;
}


.modal {
display: none;
position: fixed;
z-index: 0;
height: 100%;
width: 100%;
overflow: auto;
background-color: rgba(0,0,0,0.25);

}

.modal-content {
	display: flex;
	background-color: white;
	/* margin: 20%; auto; */
	position: relative;
	top: 50%;
	left: 50%;
	margin-left: -250px;
	margin-top: -200px;
	padding-top: 0px;
	padding-left: 0px;
	padding-right: 0px;
	box-shadow: 0 5px 8px 0 rgba(0,0,0,0.2);
	animation-name: modalopen;
	animation-duration: 1s;
	width: 550px;
	height: 450px;
	background: white;
	border: none;
	border-radius: 0px;
	z-index: 1;
}

.modalwrapper {
padding: 30px;
}

