.enter {
	background: #6E93DB	;
	width: 100%;
	height: 48px;
	border-radius: 8px;
	border: none;
	color: white;
	text-transform: none;
}

.enter:hover {
	opacity: 0.9;
}

input[type=submit] {
	background: #6E93DB	;
	width: 100%;
	height: 48px;
	border-radius: 8px;
	border: none;
	color: white;
	text-transform: none;
}

input[type=submit]:hover {
	opacity: 0.9;
}

textarea {
	width: 100%;
	height: 100px;
	padding: 12px 20px;
	outline: none;
	resize: none;
	box-sizing: border-box;
	border: 1px solid #e0e0e0;
	border-radius: 4px;
	color: grey;
	margin-top: 20px;
	margin-bottom: 20px;

}

textarea::placeholder {
	color: #BDBDBD;
	font-weight: lighter;
}

input[type=button]:focus {
	outline: none;
	border: none;
}

button:focus {
	outline: none;
}


